import React from 'react';

function Step1(props) {
    if (props.currentStep !== 1) {
        return null
    }
    return (
        <div className="form-group">
            <h3>Thm thng tin lp</h3>
            <div className="row">
                <div className="col-6">
                    <div className="form-group">
                        <label>Lp dy</label>
                        <input
                            type="text"
                            className="form-control"
                            name="classTeach"
                            value={props.classTeach} 
                            onChange={props.handleChange} required/>
                    </div>
                </div>
                <div className="col-6">
                    <div className="form-group">
                        <label>Lng (VN)</label>
                        <input
                            type="number"
                            className="form-control"
                            name="salary"
                            value={props.salary} 
                            onChange={props.handleChange} required/>
                    </div>
                </div>
            </div>
            <div className="form-group">
                <label>Mn dy</label>
                <input
                    type="text"
                    className="form-control"
                    name="subject"
                    value={props.subject} 
                    onChange={props.handleChange} required/>
            </div>
            <div className="form-group">
                <label>Thi gian dy</label>
                <input
                    type="text"
                    className="form-control"
                    name="timeTeach"
                    value={props.timeTeach} 
                    onChange={props.handleChange} required/>
            </div>
            <div className="row">
                <div className="col-8">
                    <div className="form-group">
                        <label>a ch dy</label>
                        <input
                            type="text"
                            className="form-control"
                            name="address"
                            value={props.address} 
                            onChange={props.handleChange} required/>
                    </div>
                </div>
                <div className="col-4">
                    <div className="form-group">
                        <label>Qun / Huyn</label>
                        <select required
                            className="form-control"
                            name="district"
                            value={props.district} 
                            onChange={props.handleChange}>
                            <option>Qun Th c</option>
                            <option>Qun G Vp</option>
                            <option>Qun Tn Ph</option>
                            <option>Qun Bnh Tn</option>
                            <option>Qun Ph Nhun</option>
                            <option>Qun Bnh Thnh</option>
                            <option>Qun Tn Bnh</option>
                            <option>Qun 1</option>
                            <option>Qun 2</option>
                            <option>Qun 3</option>
                            <option>Qun 4</option>
                            <option>Qun 5</option>
                            <option>Qun 6</option>
                            <option>Qun 7</option>
                            <option>Qun 8</option>
                            <option>Qun 9</option>
                            <option>Qun 10</option>
                            <option>Qun 11</option>
                            <option>Qun 12</option>
                            <option>Huyn Cn Gi</option>
                            <option>Huyn Nh B</option>
                            <option>Huyn C Chi</option>
                            <option>Huyn Bnh Chnh</option>
                            <option>Huyn Hc Mn</option>
                        </select>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-6">
                    <div className="form-group">
                        <label>Yu cu trnh </label>
                        <select required
                            className="form-control"
                            name="levelRequirement"
                            value={props.levelRequirement} 
                            onChange={props.handleChange}>
                            <option>Khng yu cu</option>
                            <option>Sinh vin</option>
                            <option>Gio vin</option>
                            <option>C nhn s phm</option>
                            <option>Thc s/Tin s</option>
                        </select>
                    </div>
                </div>
                <div className="col-6">
                    <div className="form-group">
                        <label>Yu cu gii tnh</label>
                        <select required
                            className="form-control"
                            name="genderRequirement"
                            value={props.genderRequirement} 
                            onChange={props.handleChange}>
                            <option>Khng yu cu</option>
                            <option>Nam</option>
                            <option>N</option>
                        </select>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-6">
                    <div className="form-group">
                        <label>Ph dch v</label>
                        <input
                            type="number"
                            className="form-control"
                            name="serviceFee"
                            value={props.serviceFee} 
                            onChange={props.handleChange} required/>
                    </div>
                </div>
                <div className="col-6">
                    <div className="form-group">
                        <label>Trng thi</label>
                        <select required
                            className="form-control"
                            name="status"
                            value={props.status}
                            onChange={props.handleChange}>
                            <option>Lp mi</option>
                            <option>Ch duyt</option>
                        </select>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default Step1;