import React, { Component } from 'react';
import { actGetClassRequest, actGetParentClassRequest, actUpdateClassRequest } from './../actions/index';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

class ClassDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentStep: 1,
            idClass: null,
            classTeach: '',
            subject: '',
            timeTeach: '',
            address: '',
            district: '',
            salary: null,
            serviceFee: 0.25,
            genderRequirement: 'Khng yu cu',
            levelRequirement: 'Khng yu cu',
            status: '',
            // parent
            name: '',
            phone: '',
            addressParent: '',
            email: ''
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps && nextProps.classEditing) {
            var { classEditing, parent } = nextProps;
            this.setState({
                idClass: classEditing.idClass,
                classTeach: classEditing.classTeach,
                subject: classEditing.subject,
                timeTeach: classEditing.timeTeach,
                address: classEditing.address,
                district: classEditing.district,
                salary: classEditing.salary,
                serviceFee: classEditing.serviceFee,
                genderRequirement: classEditing.genderRequirement,
                levelRequirement: classEditing.levelRequirement,
                status: classEditing.status,
                // parent
                name: parent.name,
                phone: parent.phone,
                addressParent: parent.address,
                email: parent.email
            });
        }
    }
    componentDidMount() {
        var { match } = this.props;
        if (match) {
            var id = match.params.id;
            this.props.onEditClass(id);
            this.props.onGetParentClass(id);
        }
    }

    handleChange = event => {
        const { name, value } = event.target
        this.setState({
            [name]: value
        })
    }
    onSave = (e) => {
        e.preventDefault();
        var { history } = this.props;
        var dataClass = {
            idClass: this.state.idClass,
            classTeach: this.state.classTeach,
            subject: this.state.subject,
            timeTeach: this.state.timeTeach,
            address: this.state.address,
            district: this.state.district,
            salary: this.state.salary,
            serviceFee: this.state.serviceFee,
            genderRequirement: this.state.genderRequirement,
            levelRequirement: this.state.levelRequirement,
            status: this.state.status
        }
        if (dataClass.idClass) {
            this.props.onUpdateClass(dataClass, history);
            // history.goBack();
        }
    }
    render() {
        var { classTeach, subject, timeTeach, address, district, salary, serviceFee, genderRequirement, levelRequirement, status } = this.state;
        var { name, phone, addressParent, email } = this.state;
        return (
            <div id="content-wrapper">
                <div className="container-fluid">
                    <ol className="breadcrumb">
                        <li className="breadcrumb-item">
                            <a href="dashboard">Dashboard</a>
                        </li>
                        <li className="breadcrumb-item active">Thng tin lp chi tit</li>
                    </ol>
                    <div className="row">
                        <div className="col-md-7">
                            <div className="profile">
                                <h5><strong>Thng tin lp</strong></h5>
                                <hr />
                                <form onSubmit={this.onSave}>
                                    <div className="form-group">
                                        <div className="row">
                                            <div className="col-6">
                                                <div className="form-group">
                                                    <label>Lp dy</label>
                                                    <input
                                                        type="text"
                                                        className="form-control"
                                                        name="classTeach"
                                                        value={classTeach}
                                                        onChange={this.handleChange} />
                                                </div>
                                            </div>
                                            <div className="col-6">
                                                <div className="form-group">
                                                    <label>Lng (VN)</label>
                                                    <input
                                                        type="number"
                                                        className="form-control"
                                                        name="salary"
                                                        value={salary}
                                                        onChange={this.handleChange} />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label>Mn dy</label>
                                            <input
                                                type="text"
                                                className="form-control"
                                                name="subject"
                                                value={subject}
                                                onChange={this.handleChange} />
                                        </div>
                                        <div className="form-group">
                                            <label>Thi gian dy</label>
                                            <input
                                                type="text"
                                                className="form-control"
                                                name="timeTeach"
                                                value={timeTeach}
                                                onChange={this.handleChange} />
                                        </div>
                                        <div className="row">
                                            <div className="col-8">
                                                <div className="form-group">
                                                    <label>a ch dy</label>
                                                    <input
                                                        type="text"
                                                        className="form-control"
                                                        name="address"
                                                        value={address}
                                                        onChange={this.handleChange} />
                                                </div>
                                            </div>
                                            <div className="col-4">
                                                <div className="form-group">
                                                    <label>Qun / Huyn</label>
                                                    <select
                                                        className="form-control"
                                                        name="district"
                                                        value={district}
                                                        onChange={this.handleChange}>
                                                        <option>Qun Th c</option>
                                                        <option>Qun G Vp</option>
                                                        <option>Qun Tn Ph</option>
                                                        <option>Qun Bnh Tn</option>
                                                        <option>Qun Ph Nhun</option>
                                                        <option>Qun Bnh Thnh</option>
                                                        <option>Qun Tn Bnh</option>
                                                        <option>Qun 1</option>
                                                        <option>Qun 2</option>
                                                        <option>Qun 3</option>
                                                        <option>Qun 4</option>
                                                        <option>Qun 5</option>
                                                        <option>Qun 6</option>
                                                        <option>Qun 7</option>
                                                        <option>Qun 8</option>
                                                        <option>Qun 9</option>
                                                        <option>Qun 10</option>
                                                        <option>Qun 11</option>
                                                        <option>Qun 12</option>
                                                        <option>Huyn Cn Gi</option>
                                                        <option>Huyn Nh B</option>
                                                        <option>Huyn C Chi</option>
                                                        <option>Huyn Bnh Chnh</option>
                                                        <option>Huyn Hc Mn</option>
                                                    </select>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="row">
                                            <div className="col-6">
                                                <div className="form-group">
                                                    <label>Yu cu trnh </label>
                                                    <select
                                                        className="form-control"
                                                        name="levelRequirement"
                                                        value={levelRequirement}
                                                        onChange={this.handleChange}>
                                                        <option>Khng yu cu</option>
                                                        <option>Sinh vin</option>
                                                        <option>Gio vin</option>
                                                        <option>C nhn s phm</option>
                                                        <option>Thc s/Tin s</option>
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="col-6">
                                                <div className="form-group">
                                                    <label>Yu cu gii tnh</label>
                                                    <select
                                                        className="form-control"
                                                        name="genderRequirement"
                                                        value={genderRequirement}
                                                        onChange={this.handleChange}>
                                                        <option>Khng yu cu</option>
                                                        <option>Nam</option>
                                                        <option>N</option>
                                                    </select>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-6">
                                                <div className="form-group">
                                                    <label>Ph dch v</label>
                                                    <input
                                                        type="number"
                                                        className="form-control"
                                                        name="serviceFee"
                                                        value={serviceFee}
                                                        onChange={this.handleChange} />
                                                </div>
                                            </div>
                                            <div className="col-6">
                                                <div className="form-group">
                                                    <label>Trng thi</label>
                                                    <select
                                                        className="form-control"
                                                        name="status"
                                                        value={status}
                                                        onChange={this.handleChange}>
                                                        <option>Lp mi</option>
                                                        <option>Ch duyt</option>
                                                        <option>Khng t</option>
                                                        <option> giao</option>
                                                    </select>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <Link to="/class-list" className="btn btn-secondary">
                                        HY
                                    </Link>
                                    <button type="submit" className="btn btn-primary" style={{ float: 'right' }}>LU</button>
                                </form>
                            </div>
                        </div>
                        <div className="col-md-5">
                            <div className="profile">
                                <h5><strong>Thng tin ph huynh</strong></h5>
                                <div style={{ textAlign: 'left' }}>
                                    <hr />
                                    <form>
                                        <div className="form-group">
                                            <label>H tn</label>
                                            <input type="text" className="form-control" value={name} />
                                        </div>
                                        <div className="form-group">
                                            <label >S in thoi</label>
                                            <input type="number" className="form-control" value={phone} />
                                        </div>
                                        <div className="form-group">
                                            <label >a ch</label>
                                            <input type="text" className="form-control" value={addressParent} />
                                        </div>
                                        <div className="form-group">
                                            <label>Email (nu c)</label>
                                            <input type="email" className="form-control" value={email} />
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch, props) => {
    return {
        onEditClass: (id) => {
            dispatch(actGetClassRequest(id));
        },
        onGetParentClass: (id) => {
            dispatch(actGetParentClassRequest(id));

        },
        onUpdateClass: (classs, history) => {
            dispatch(actUpdateClassRequest(classs, history));
        }
    }
}
const mapStateToProps = state => {
    return {
        classEditing: state.classEditing,
        parent: state.parent
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClassDetail);